* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 20px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
.countries-list,
.country-list {
  padding: 0;
}

li {
  list-style: none;
}

.country-name {
  font-size: 24px;
  font-weight: 600;
  padding-left: 30px;
}

.country-name-one {
  font-size: 48px;
  font-weight: 700;
  padding-left: 15px;
}

.country-description {
  font-size: 28px;
  font-weight: 700;
}

.country-description-little {
  font-weight: 400;
}

.language {
  display: inline;
  font-weight: 400;
}

.language:not(:last-child)::after {
  content: ', ';
}
